@use '../../foundation/_variable' as *;

.p-fv {
    display: block;
    width: 100%;
    padding-top: 10rem;
    &__catch {
        font-size: 2rem;
    }
    &__title {
        padding: 10rem 2rem 12rem;
        background: url("https://placehold.jp/350x350.png") center / cover no-repeat;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        font-size: 2.8rem;
        position: relative;
        z-index: 99;
    }
    &__img {
        position: absolute;
        bottom: -50%;
        left: 50%;
        transform: translateX(-50%) translateY(-25%);
        outline: 1px solid $color-font;
        z-index: 9;
    }
    &__list {
        background-color: rgb(106, 73, 73);
    }
    &__listItems {
        display: flex;
        justify-content: space-around;
        position: relative;
        z-index: 99;
        padding-top: 8rem;
        li {
            background: $color-base;
            border: 2px solid $color-font;
            height: 12rem;
            width: 16rem;
            border-radius: 4.5rem 0 0 0;
            padding: 2.4rem 1.6rem 1.6rem 2.4rem;
            font-size: 1.4rem;
            font-weight: bold;
        }
    }
    &__underline {
        background: linear-gradient(transparent 50%, yellow 50%);
        line-height: 1;
    }
    &__note {
        font-size: 1.2rem;
        color: $color-base;
        padding: 0.5rem 0 1rem 1rem;
    }
}