@use '../foundation/_variable' as *;

//アコーディオン
.p-faq .js-accordion {
    &__active {
        visibility: inherit;
        max-height: 50rem;
        transition: visibility .3s, max-height .3s;
        padding: 2.3rem 8.7rem 2.3rem 3.5rem;
        @include breakpoint(large) {
            padding: 2.3rem 3.5rem;
        }
        @include breakpoint(small) {
            padding: 2.3rem 2.4rem;
        }
    }
}

//scroll ふわっと表示
.js-scroll__target {
    opacity: 0;
    transform: translate3d(0, 30px, 0);
}
.js-scroll__target.is-active {
    opacity: 1;
    transform: translate3d(0, 0, 0);
    transition: 1s;
}