// colors
$color-base: #fff;
$color-background: #EEFBFB;
$color-font: #333333;
$color-green: green;
$color-orange: orange;
$color-yellow: yellow;
$color-red: red;
$color-black: black;
$color-darkgray: darkgray;
$color-gray: gray;

// responsive
@mixin breakpoint($point) {
	@if $point == small {
		@media (max-width: 576px) {
			@content;
		}
	}
	@if $point == medium {
		@media (max-width: 768px) {
			@content;
		}
	}
	@if $point == large {
		@media (max-width: 992px) {
			@content;
		}
	}
	@if $point == x-large {
		@media (max-width: 1200px) {
			@content;
		}
	}
	@if $point == xx-large {
		@media (max-width: 1400px) {
			@content;
		}
	}
}
@include breakpoint(small) {
}
@include breakpoint(medium) {
}
@include breakpoint(large) {
