@use '../../foundation/_variable' as *;

.c-btn {
    display: block;
    width: 20rem;
    height: 4.8rem;
    font-size: 1.6rem;
    font-weight: bold;
    color: $color-base;
    background: $color-green;
    padding: 1rem;
    border-radius: 2.4rem;
    box-shadow: 0px 3px 6px $color-gray;
    text-align: center;
}
